@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{width:100%;height:100%;font-family:"Helvetica Neue",Helvetica,sans-serif;color:#444;-webkit-font-smoothing:antialiased;background:#fff;background-image:url("https://i.ya-webdesign.com/images/abstract-background-png-3.png") !important;background-repeat:no-repeat !important}.App{text-align:center;justify-content:center;align-items:center;font-family:"Open Sans",sans-serif;width:100% !important;background-repeat:no-repeat !important}.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}
.bg-image{height:100%;width:100%}.image{width:21em}.image img{width:100%;height:100%}.container{position:fixed;padding-top:5%;width:340px;margin-left:41%}form{margin:0 auto;margin-top:20px}label{color:#fff;margin-left:18px;padding-top:10px;font-size:13px}input{font-family:"Helvetica Neue",Helvetica,sans-serif;font-size:12px;outline:none}input[type=text],input[type=password]{color:#777;padding-left:10px;margin:10px;margin-top:12px;margin-left:18px;width:290px;height:35px;border:1px solid #c7d0d2;border-radius:2px;box-shadow:inset 0 1.5px 3px rgba(190,190,190,.4),0 0 0 5px #f5f7f8;transition:all .4s ease}#lower{width:50%;height:auto;margin-top:20px;margin-left:27%;box-shadow:inset 0 1px 1px #fff;border-top:2px solid #ccc;border-bottom-right-radius:3px;border-bottom-left-radius:3px}.loginBtn{outline:none;border:1px solid #57d9eb56;border-radius:7px;margin-top:20px;width:80px;height:30px;font-size:14px;font-weight:bold;color:#003cff;background-color:#60e5f786;cursor:pointer}.loginBtn:hover{background-color:rgba(88,118,216,.651)}#title{color:#fff;font-size:17px}.btn{transition-duration:.4s;margin-left:20px;margin-top:20px;width:auto;height:30px;font-size:14px;font-weight:bold;color:#fff;background-color:#4eafe700;border:none;cursor:pointer}.btn:hover{color:#003cff;background-color:rgba(255,255,255,0)}.Error{background-color:#e27373a4;box-shadow:0 1px 2px rgba(0,0,0,.3),inset 0 1px 0 #fff;font-size:14px;color:#fff;height:21px !important;margin-bottom:10px;margin-top:10px}.forgotPass{border:none;transition-duration:.4s;width:auto;height:30px;font-size:14px;font-weight:bold;color:#fff;background-color:#4eafe700;cursor:pointer;outline:none}.forgotPass:hover{color:#003cff;background-color:rgba(255,255,255,0);outline:none}.resset-container{height:100%;width:100%;background-color:transparent !important}.resset-position{padding:1%;background-image:radial-gradient(#4688b4, transparent);margin-top:13%;margin-left:13%;width:74%}.email-input{width:100%;height:30px;border:none;opacity:none;background-color:rgba(255,255,255,.267);color:#105566;font-size:18px}.email-input:hover{background-color:rgba(255,255,255,.767)}h1{color:#105566}.ressetBtn{height:30px;background-color:rgba(255,255,255,0);border:none;outline:none;font-size:15px;color:#fff;margin:10px}.resset-tittle{background-color:rgba(102,51,153,0)}.ressetBtn:hover{cursor:pointer;color:#105566;border:none;outline:none}h5{color:#fff;font-weight:normal}
.nav{background:rgba(26,195,201,0);height:60px;width:100%;margin-left:2%}.nav li{display:inline-block;list-style:none;height:60px;line-height:60px;padding:0 40px}.nav-link:hover{color:#07058f}.nav-link{font-size:20px;transition:ease-in;color:#00f0f8;text-decoration:none}.logout-btn{float:right;margin-top:2%;margin-right:4%;background:rgba(26,195,201,0);border:none;cursor:pointer;color:#07058f;font-size:15px}.logout-btn:hover{border:none;color:#0e8ad1}.navbar{width:100%}
